import React from "react";
import TodoListItem from "../todo-list-item/todo-list-item";

const TodoList = () => {
  const items = ['Learn React', 'Build Awesome App', 'Privet Savva'];
  return (
    <ul>
      { items.map((item, key) => <li key={key}><TodoListItem name={item}/></li>) }
    </ul>
  );
};

export default TodoList;
